import { first, select } from "~/core/database";
import * as sqls from "./sqls";

export const get = async ({ id }) =>
  Promise.all([
    first(
      `
        ${sqls.select}
        ${sqls.from}
        where U.id = ?
      `,
      [id]
    ),
    select(sqls.classTypes, [id]),
    select(sqls.subjects, [id]),
  ]).then(([student, classTypes, subjects]) => ({ ...student, classTypes, subjects }));
